/* Stili per le transizioni tra pagine */

/* Animazioni di base per le transizioni di pagina */
.page-transition {
    animation-duration: 0.5s;
    animation-fill-mode: both;
  }
  
  .page-enter {
    animation-name: fadeIn;
  }
  
  .page-exit {
    animation-name: fadeOut;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes fadeOut {
    from {
      opacity: 1;
      transform: translateY(0);
    }
    to {
      opacity: 0;
      transform: translateY(-20px);
    }
  }
  
  /* Animazione di navigazione per le card */
  .card-transition {
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  }
  
  .card-transition:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 30px rgba(0, 255, 127, 0.1);
  }
  
  /* Loader di transizione */
  .page-loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
  }
  
  .loader-content {
    text-align: center;
  }
  
  .matrix-symbol {
    font-family: monospace;
    font-size: 3rem;
    color: #00ff7f;
    margin-bottom: 1rem;
    animation: blink 1s infinite;
  }
  
  @keyframes blink {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.5;
    }
  }
  
  .loader-bar {
    width: 200px;
    height: 4px;
    background-color: #222;
    border-radius: 2px;
    overflow: hidden;
    margin: 0 auto;
  }
  
  .loader-progress {
    height: 100%;
    width: 100%;
    background: linear-gradient(90deg, #00ff7f, #059669);
    animation: progress 1.5s ease-in-out infinite;
    transform-origin: left;
  }
  
  @keyframes progress {
    0% {
      transform: scaleX(0);
      transform-origin: left;
    }
    49% {
      transform: scaleX(1);
      transform-origin: left;
    }
    50% {
      transform: scaleX(1);
      transform-origin: right;
    }
    100% {
      transform: scaleX(0);
      transform-origin: right;
    }
  }
  
  /* Transizione per la barra di navigazione */
  .navbar-transition {
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
  }
  
  .navbar-scrolled {
    background-color: rgba(17, 24, 39, 0.95) !important;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  }
  
  /* Transizione per il modal */
  .modal-transition {
    animation: modalFadeIn 0.3s ease forwards;
  }
  
  @keyframes modalFadeIn {
    from {
      opacity: 0;
      transform: scale(0.95) translateY(-10px);
    }
    to {
      opacity: 1;
      transform: scale(1) translateY(0);
    }
  }
  
  .modal-backdrop {
    animation: backdropFadeIn 0.2s ease forwards;
  }
  
  @keyframes backdropFadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  /* Transizione per la pagina MasteryHub */
  .skills-section-enter {
    animation: slideInUp 0.5s forwards;
  }
  
  @keyframes slideInUp {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .skill-bar-animation {
    animation: fillBar 1.5s ease-out forwards;
  }
  
  @keyframes fillBar {
    from {
      width: 0;
    }
    to {
      width: var(--bar-width);
    }
  }
  
  /* Transizione per i progetti */
  .project-card-enter {
    animation: projectEnter 0.5s forwards;
    animation-delay: calc(var(--index) * 0.1s);
    opacity: 0;
  }
  
  @keyframes projectEnter {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Effetto hover per i tag */
  .tag-hover {
    transition: all 0.2s ease;
  }
  
  .tag-hover:hover {
    background-color: rgba(5, 150, 105, 0.3);
    transform: translateY(-2px);
  }