@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=DotGothic16&display=swap');

body {
  background-color: black;
  color: #00ff00;
  overflow-x: hidden; /* Solo overflow-x hidden per permettere lo scroll verticale */
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  min-height: 100vh; /* Assicura che il body occupi almeno l'intera altezza della viewport */
}

.navbar-gradient, .footer-gradient {
  background: linear-gradient(0deg, #059669 85%, rgba(5, 150, 105, 0) 100%);
  width: 100%; /* Assicura che la navbar e il footer occupino l'intera larghezza */
}

@keyframes pulse-custom {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(37, 99, 235, 0.7); /* Ombra blu */
  }
  70% {
    transform: scale(1.05);
    box-shadow: 0 0 0 10px rgba(37, 99, 235, 0);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(37, 99, 235, 0);
  }
}

.pulse-animation {
  animation: pulse-custom 2s infinite;
}

.pulse-animation:hover {
  animation: none;
}

@keyframes slideInFade {
  0% {
    opacity: 0;
    transform: translateX(50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.slide-in-section {
  animation: slideInFade 1.5s ease-out forwards;
  width: 100%; /* Assicura che la sezione occupi l'intera larghezza del contenitore */
}

.slide-in-section h1 {
  opacity: 0;
  animation: slideInFade 1.5s ease-out forwards;
  animation-delay: 0.5s;
}

.slide-in-section p {
  opacity: 0;
  animation: slideInFade 1.5s ease-out forwards;
  animation-delay: 1s;
}

@keyframes dealCard {
  0% {
    opacity: 0;
    transform: translateY(-100px) translateX(-50px) rotate(-20deg);
  }
  60% {
    transform: translateY(20px) translateX(10px) rotate(5deg);
  }
  80% {
    transform: translateY(-10px) translateX(-5px) rotate(-2deg);
  }
  100% {
    opacity: 1;
    transform: translateY(0) translateX(0) rotate(0);
  }
}

.card-trigger {
  opacity: 0;
  width: 100%; /* Assicura che le card occupino l'intera larghezza disponibile */
}

.card-animate {
  animation: dealCard 1.5s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

.floating-element {
  animation: float 3s ease-in-out infinite;
}

@keyframes gradient-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.animated-circle {
  width: 200px;
  height: 200px;
  background: linear-gradient(45deg, #00ff7f, #4F4A4A);
  border-radius: 50%;
  animation: gradient-spin 8s linear infinite;
}

.animated-triangle {
  width: 0;
  height: 0;
  border-left: 100px solid transparent;
  border-right: 100px solid transparent;
  border-bottom: 173.2px solid #00ff7f;
  position: relative;
  animation: gradient-spin 8s linear infinite;
}

.animated-square {
  width: 200px;
  height: 200px;
  background: linear-gradient(45deg, #00ff7f, #4F4A4A);
  animation: gradient-spin 8s linear infinite;
}

.footer-gradient {
  background: linear-gradient(0deg, #059669 85%, rgba(5, 150, 105, 0) 100%);
  width: 100%; /* Assicura che il footer occupi l'intera larghezza */
}

.overflow-y-auto::-webkit-scrollbar {
  width: 8px;
}

.overflow-y-auto::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.overflow-y-auto::-webkit-scrollbar-thumb {
  background: rgba(0, 255, 127, 0.3);
  border-radius: 4px;
}

.overflow-y-auto::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 255, 127, 0.5);
}

