/* Aggiungiamo nuovi stili per l'effetto matrix migliorato */
.enhanced-matrix-effect {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  pointer-events: none; /* Permette di cliccare attraverso il canvas */
}

/* Miglioriamo il contrasto per i contenuti con una transizione più morbida */
.dark .text-emerald-300 {
  text-shadow: 0 0 8px rgba(0, 255, 127, 0.3);
}

/* Aggiungiamo un effetto glowing per i titoli quando l'animazione è attiva */
.dark h1, .dark h2, .dark h3 {
  transition: text-shadow 0.3s ease;
}

.dark h1:hover, .dark h2:hover, .dark h3:hover {
  text-shadow: 0 0 12px rgba(0, 255, 127, 0.6);
}

/* Rendiamo le card un po' più traslucide per far vedere l'animazione dietro */
.dark .card-trigger {
  background-color: rgba(38, 38, 38, 0.7); /* Sfondo più trasparente */
  backdrop-filter: blur(5px); /* Effetto frosted glass */
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.dark .card-trigger:hover {
  background-color: rgba(38, 38, 38, 0.85); /* Un po' più opaco all'hover */
}

/* Media queries migliorati per il responsive design */
@media (max-width: 768px) {
  .slide-in-section {
    animation-delay: 0.2s;
  }
  
  .slide-in-section h1 {
    animation-delay: 0.4s;
  }
  
  .slide-in-section p {
    animation-delay: 0.6s;
  }
  
  @keyframes dealCard {
    0% {
      opacity: 0;
      transform: translateY(-50px);
    }
    60% {
      transform: translateY(10px);
    }
    80% {
      transform: translateY(-5px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
  }
  
  /* Aggiustamenti per i contenitori principali */
  .navbar-gradient, .footer-gradient {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

/* Fix for very small screens */
@media (max-width: 480px) {
  .animated-circle, .animated-triangle, .animated-square {
    transform: scale(0.7);
  }
  
  /* Aggiustamenti per testo e spaziatura su schermi molto piccoli */
  h1, h2 {
    font-size: 1.5rem;
  }
  
  p {
    font-size: 0.875rem;
  }
  
  .p-4 {
    padding: 0.75rem;
  }
}